import React from "react"
import { Link, graphql } from "gatsby"
import Monkey from "../components/monkey_msg"
import Layout from "../components/layout"

export default ({ data }) => {

  const textStyle = {
    textDecoration: 'none',
    color: 'black',
    fontFamily: 'Gentium Book Basic',
  }
  const post = data.markdownRemark
  const msg = post.frontmatter.monkey_msg
  return (
    <Layout>
      <div>
        <Link to={post.frontmatter.next} style={{ textDecoration: 'none' }}>
          <div style={textStyle} dangerouslySetInnerHTML={{ __html: post.html }} />
          { msg.length > 0 &&
            <Monkey msg={msg} />
          }
        </Link>
      </div>
      <Link to={post.frontmatter.previous}>previous{"<<<"}</Link>
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        previous
        next
        monkey_msg
      }
    }
  }
`
